@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=PT+Serif:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bitter:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Merriweather:wght@700&display=swap");
* {
  box-sizing: border-box;
}

ul {
  list-style: none;
  padding: 0;
}

a {
  text-decoration: none;
}

body {
  width: 80%;
  margin: 0px;
  margin-left: 10%;
}

header {
  margin-top: 20px;
}
header .header__top {
  width: inherit;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px dashed var(--grey);
}
header .header__top .sections {
  position: relative;
  width: auto;
  gap: 8px;
  display: flex;
  align-items: center;
}
header .header__top .sections h2 {
  font-family: "Roboto";
  font-weight: 500;
  margin: 0;
}
header .header__top .center-h2 {
  font-family: "PT Serif";
  font-weight: 400;
  font-size: 20px;
  opacity: 0.5;
}
header .header__top .right-part {
  display: flex;
  align-items: center;
  gap: 20px;
}
header .header__top .right-part .search {
  display: inline-flex;
  align-items: center;
  gap: 5px;
}
header .header__top .right-part .search img {
  width: 13px;
  height: 13px;
}
header .header__top .right-part .search h2 {
  font-family: "Roboto";
  font-weight: 500;
  margin: 0;
  font-size: 14px;
  line-height: 15px;
}
header .header__top .right-part .login {
  width: 75px;
  height: 30px;
  border: none;
  cursor: pointer;
}
header .header__top .right-part .login h2 {
  font-family: "Roboto";
  font-weight: 400;
  margin: 0;
  font-size: 14px;
  line-height: 15px;
}
header h1 {
  font-family: "Bitter";
  font-weight: 700;
  font-size: 64px;
  letter-spacing: -4px;
  text-align: center;
  margin: 10px 0;
  margin-bottom: 25px;
}
header .topics {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--grey);
  border-top: 1px solid var(--grey);
  margin-bottom: 40px;
}
header .topics__invisible-div {
  width: 76px;
}
header .topics ul {
  display: inline-flex;
  gap: 7.5px;
  justify-content: space-between;
}
header .topics ul a {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 25px;
  color: #000;
}
header .topics a {
  display: flex;
  align-items: center;
  gap: 5px;
  font-family: "Roboto";
  font-weight: 700;
  color: var(--link-color);
  font-size: 13px;
}
header .topics a img {
  width: 20px;
  height: 16px;
}

.sections__button {
  width: 42px;
  height: 36px;
  padding: 7px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
  transition: 0.5s all ease-in-out;
  border-radius: 5px;
}
.sections__button div {
  width: 28px;
  height: 2px;
  background-color: #000;
}

.sections__button:hover {
  background-color: var(--grey);
}

.menu-wrapper-hidden {
  display: none;
}

.menu-wrapper {
  display: flex;
  position: absolute;
  z-index: 100000000000000000;
  width: 100%;
  height: 100%;
  flex-direction: row;
  justify-content: space-between;
  left: 0;
  top: 0;
}
.menu-wrapper .menu {
  min-width: 280px;
  width: 20%;
  height: 100%;
  background-color: #fff;
  padding-left: 30px;
  padding-top: 15px;
}
.menu-wrapper .menu__top {
  width: 90%;
  display: flex;
  justify-content: space-between;
}
.menu-wrapper .menu__top .sections__button2 {
  width: 42px;
  height: 36px;
  padding: 7px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
  transition: 0.5s all ease-in-out;
  border-radius: 5px;
}
.menu-wrapper .menu__top .sections__button2 div {
  width: 28px;
  height: 2px;
  background-color: #000;
}
.menu-wrapper .menu__top .sections__button2 div:first-child {
  transform: rotate(45deg);
  margin-top: 0;
}
.menu-wrapper .menu__top .sections__button2 div:last-child {
  margin-top: -22px;
  transform: rotate(135deg);
}
.menu-wrapper .menu__top .button:hover {
  background-color: var(--grey);
}
.menu-wrapper .menu__top h2 {
  margin: 0;
  font-family: "Roboto";
  font-weight: 500;
  display: flex;
  align-items: center;
}
.menu-wrapper .menu ul {
  width: 87%;
}
.menu-wrapper .menu ul li {
  width: 100%;
  font-family: "Roboto";
  font-weight: 500;
  padding: 10px 0;
  border-top: 1px solid #000;
}
.menu-wrapper .menu ul li a {
  color: #000;
}
.menu-wrapper__body-background {
  width: 80%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  opacity: 1;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
}

main {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
main .main-content {
  width: 100%;
}
main .main-content img {
  width: inherit;
  z-index: 0;
}
main .main-content__title {
  position: relative;
  width: 77%;
  height: auto;
  background-color: #fff;
  padding: 10px;
  z-index: 9999999;
  margin-top: -70px;
  margin-left: 20px;
}
main .main-content__title h2 {
  width: 100%;
  height: auto;
  font-family: "PT Serif";
  font-weight: 700;
  font-size: 27px;
  margin: 0;
}
main .main-content p {
  font-family: "Roboto";
  font-weight: 400;
  margin-left: 20px;
  font-size: 15px;
}
main .top-stories {
  width: 78%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0 20px;
}
main .top-stories mark {
  padding: 10px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 20px;
}
main .top-stories .top__view {
  display: none;
}
main .top-stories ul {
  margin-bottom: 0;
}
main .top-stories ul li {
  font-family: "PT Serif";
  font-weight: 400;
  font-size: 21px;
  padding: 10px 0;
  padding-right: 10px;
}
main .top-stories ul .border {
  border-top: 1px solid var(--grey);
}
main .top-stories #top-stories__ul-first {
  display: none;
}
main .quick-bites {
  width: 80%;
  height: auto;
  padding-left: 20px;
}
main .quick-bites__stocks {
  background-color: var(--grey);
  height: auto;
}
main .quick-bites__stocks mark {
  position: relative;
  top: 10px;
  padding: 10px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 20px;
}
main .quick-bites__stocks h3 {
  width: 70%;
  margin-left: 7.5%;
  margin-top: 40px;
  font-family: "Merriweather";
  font-weight: 700;
  font-size: 24px;
}
main .quick-bites__stocks p {
  width: 84%;
  margin-left: 7%;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 22px;
}
main .quick-bites__stocks a {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding-right: 30px;
  padding-bottom: 20px;
}
main .quick-bites__stocks a .text {
  font-family: "Roboto";
  font-weight: 700;
  font-size: 20px;
  color: var(--link-color);
}
main .quick-bites__stocks a .arrow-right {
  display: block;
  margin-top: 7px;
  width: 11px;
  height: 11px;
  border-top: 2px solid var(--link-color);
  border-left: 2px solid var(--link-color);
  transform: rotate(135deg);
}
main .quick-bites .form-title {
  width: 83%;
  font-family: "Merriweather";
  font-weight: 700;
  font-size: 24px;
  color: var(--link-color);
}
main .quick-bites form {
  width: 100%;
  display: inline-flex;
  justify-content: space-between;
}
main .quick-bites form input[type=email] {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 20px;
  border: none;
  outline: none;
}
main .quick-bites form input[type=submit] {
  background-color: var(--link-color);
  border: none;
  color: #fff;
  padding: 7px 15px;
  cursor: pointer;
}

:root {
  --grey: #d8d8d8;
  --link-color: #0C79FF;
}

@media (max-width: 1540px) {
  .topics__invisible-div {
    display: none;
  }
}
@media (max-width: 1440px) {
  body {
    width: 90%;
    margin-left: 5%;
  }
}
@media (max-width: 1275px) {
  .topics ul li a {
    font-size: 23px;
  }
}
@media (max-width: 1200px) {
  .topics {
    width: 100%;
  }
  .topics ul {
    width: 81%;
  }
  .topics ul li a {
    font-size: 20px;
  }
}
@media (max-width: 990px) {
  .menu-wrapper .menu {
    width: 45%;
  }
  header h1 {
    margin: 10px;
  }
  header .topics ul a {
    font-size: clamp(13px, 100%, 20px);
  }
  main .main-content__title {
    width: 85%;
  }
  main .main-content__title h2 {
    font-size: 20px;
  }
  main .main-content p {
    margin-top: 5px;
    font-size: 12px;
  }
  main .top-stories mark {
    font-size: 17.5px;
  }
  main .top-stories ul {
    margin-top: 0;
  }
  main .top-stories ul li {
    font-size: 15px;
  }
  main .quick-bites {
    padding: 0;
  }
  main .quick-bites__stocks mark {
    font-size: 17.5px;
  }
  main .quick-bites__stocks h3 {
    width: 85%;
    font-size: 20px;
    margin-top: 25px;
    margin-bottom: 0;
    margin-left: 15px;
  }
  main .quick-bites__stocks p {
    font-size: 14.5px;
    margin: 7px 15px;
    margin-bottom: 0;
  }
  main .quick-bites__stocks a {
    padding-bottom: 10px;
  }
  main .quick-bites__stocks a .text {
    font-size: 17.5px;
  }
  main .quick-bites .form-title {
    width: 95%;
    margin: 10px 0;
    font-size: 20px;
  }
}
@media (max-width: 870px) {
  main .main-content__title {
    width: 90%;
  }
  main .main-content__title h2 {
    font-size: 17.5px;
  }
  main .main-content__title, main .main-content p {
    margin-left: 10px;
  }
  main .top-stories mark {
    font-size: 17.5px;
  }
  main .top-stories ul {
    margin-top: 0;
  }
  main .top-stories ul li {
    padding-right: 0;
    font-size: 14px;
  }
}
@media (max-width: 815px) {
  header h1 {
    line-height: 64px;
    margin: 7px;
  }
  header .topics {
    margin-bottom: 20px;
  }
  header .topics ul a {
    font-size: clamp(13px, 95%, 20px);
  }
}
@media (max-width: 800px) {
  main .top-stories ul li {
    font-size: 13px;
  }
}
@media (max-width: 780px) {
  header h1 {
    line-height: 54px;
    font-size: 54px;
    margin: 7px;
  }
  header .topics ul a {
    font-size: clamp(13px, 90%, 20px);
  }
  main .top-stories ul {
    margin-top: 10px;
  }
  main .top-stories ul li {
    font-size: 12px;
  }
}
@media (max-width: 767px) {
  main {
    display: flex;
    flex-direction: column;
  }
  main .main-content__title h2 {
    font-size: 27px;
  }
  main .main-content p {
    display: none;
  }
  main .top-stories {
    width: 100%;
    margin: 10px 0;
    padding: 0;
  }
  main .top-stories .top {
    width: inherit;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  main .top-stories .top__view {
    display: flex;
    color: var(--link-color);
    align-items: center;
    font-family: "Roboto";
    font-size: 17.5px;
  }
  main .top-stories ul {
    width: 100%;
  }
  main .top-stories #top-stories__ul-first {
    display: block;
  }
  main .top-stories #top-stories__ul-first li {
    font-size: clamp(15px, 5vh, 31px);
  }
  main .top-stories #top-stories__ul-second {
    display: none;
  }
  main .top-stories #top-stories__ul-second li {
    font-size: 27px;
  }
  main .quick-bites {
    width: 100%;
  }
  main .quick-bites__stocks h3, main .quick-bites__stocks p {
    margin-bottom: 10px;
  }
  main .quick-bites__stocks h3 {
    width: 90%;
    font-size: 28px;
  }
  main .quick-bites__stocks p {
    font-size: 20px;
  }
  main .quick-bites__stocks a .text {
    line-height: 22.5px;
    font-size: 22.5px;
  }
  main .quick-bites__stocks a .arrow {
    margin-top: -105px;
  }
  main .quick-bites .form-title {
    font-size: 27px;
    margin: 20px 0;
  }
  main .quick-bites form {
    margin-bottom: 10px;
  }
  main .quick-bites form input[type=email] {
    font-size: 25px;
  }
}
@media (max-width: 746px) {
  header .topics ul li {
    font-size: clamp(10px, 1vh, 20px);
  }
  main .top-stories #top-stories__ul-first li {
    font-size: clamp(15px, 175%, 31px);
  }
}
@media (max-width: 690px) {
  header .topics ul {
    gap: 5px;
  }
  header .topics ul li {
    font-size: 0.6rem;
  }
  main .top-stories #top-stories__ul-second li {
    font-size: 1.35rem;
  }
}
@media (max-width: 674px) {
  header .topics ul {
    gap: 4px;
  }
  header .topics ul li a {
    font-size: 0.75rem;
  }
  main .top-stories #top-stories__ul-first li {
    font-size: 1.5rem;
  }
}
@media (max-width: 620px) {
  header .header__top {
    border-bottom: none;
    margin-bottom: 10px;
  }
  header .header__top h2, header .header__top .login {
    display: none;
  }
  header .header__top .right-part .search img {
    width: 15px;
    height: 15px;
  }
  header h1 {
    position: absolute;
    top: 10px;
    left: calc(30% + 10px);
  }
  header .topics {
    display: none;
  }
}